import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable({
  providedIn: 'root'
})
export class ConnectionService {

 // baseurl="http://163.122.226.25:2000/";
  baseurl="https://ibegin.herokuapp.com/";
  
  constructor(private http: Http) { }

  login(data){
    let headers = new Headers({'Content-Type': 'application/json'});
    return this.http.post(this.baseurl+"login",data,{headers:headers}).map(res => {       
      return res.json();  
     });
  }

  getquery(){
    let headers = new Headers({'Content-Type': 'application/json','x-access-token':window.localStorage.getItem('x-access-token')});
    return this.http.post(this.baseurl+"getdata",{},{headers:headers}).map(res => {       
      return res.json();  
     });
  }

  getuser(){
    let headers = new Headers({'Content-Type': 'application/json','x-access-token':window.localStorage.getItem('x-access-token')});
    return this.http.get(this.baseurl+"alluser",{headers:headers}).map(res => {       
      return res.json();  
     });
  }

  getresponse(data){
    let headers = new Headers({'Content-Type': 'application/json','x-access-token':window.localStorage.getItem('x-access-token')});
    return this.http.get(this.baseurl+"chatbot?text="+data,{headers:headers}).map(res => {       
      return res.json();  
     });
  }

  deletequery(data){
    let headers = new Headers({'Content-Type': 'application/json','x-access-token':window.localStorage.getItem('x-access-token')});

    return this.http.get(this.baseurl+"deletequestion?list="+data,{headers:headers}).map(res => {       
      return res.json();  
     });
  }


  savequery(data){
    
    let headers = new Headers({'Content-Type': 'application/json','x-access-token':window.localStorage.getItem('x-access-token')});
    return this.http.post(this.baseurl+"writedata",{data:data},{headers:headers}).map(res => {       
      return res;  
     });
  }
  addandedit(data){
    
    let headers = new Headers({'Content-Type': 'application/json','x-access-token':window.localStorage.getItem('x-access-token')});
    return this.http.post(this.baseurl+"addandedit",{data:data},{headers:headers}).map(res => {       
      return res;  
     });
  }

  getnewquery(){
    let headers = new Headers({'Content-Type': 'application/json','x-access-token':window.localStorage.getItem('x-access-token')});
    return this.http.get(this.baseurl+"questions",{headers:headers}).map(res => {       
      return res.json();  
     });
  }

  savenewquery(data){
    
    let headers = new Headers({'Content-Type': 'application/json','x-access-token':window.localStorage.getItem('x-access-token')});
    return this.http.post(this.baseurl+"savequery",{data:data},{headers:headers}).map(res => {       
      return res;  
     });
  }

  download(){
    let headers = new Headers({'Content-Type': 'application/json','x-access-token':window.localStorage.getItem('x-access-token')});
    return this.http.get(this.baseurl+"downloaduser",{headers:headers}).map(res => {       
      return res;  
     });
  }
  
  resetuser(){
    let headers = new Headers({'Content-Type': 'application/json','x-access-token':window.localStorage.getItem('x-access-token')});
    return this.http.get(this.baseurl+"reset",{headers:headers}).map(res => {       
      return res.json();  
     });
  }

  addmanynewquery(data){
    let headers = new Headers({'Content-Type': 'application/json','x-access-token':window.localStorage.getItem('x-access-token')});
    return this.http.post(this.baseurl+"savenewintent",{data:data},{headers:headers}).map(res => {       
      return res.json();  
     });

  }
  changepassword(data){
    let headers = new Headers({'Content-Type': 'application/json','x-access-token':window.localStorage.getItem('x-access-token')});
    return this.http.post(this.baseurl+"savenewintent",data,{headers:headers}).map(res => {       
      return res.json();  
     });
  }
}
