import { Component, OnInit } from '@angular/core';
import { ConnectionService } from '../connection.service';
import { SpeechRecognitionService } from '../speech-recognition.service';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.scss'],
  providers:[SpeechRecognitionService]
})
export class ChatComponent implements OnInit {

  mikevalue=true;
  loadingchat=false;
  checkbox1=false;
  conversation: any = [
    { "msg":"Hi, I am Emma your help assistant how may i help you", "sentBy": "CB" }
  ];
  input;
  
  constructor(public service:ConnectionService,public speak:SpeechRecognitionService) {
    // this.speak.speak("Hi, I am Emma your help assistant how may i help you",(err)=>{
    //   this.startlisen();
    //     })
   }

  speakPromise: (speech: any)=>Promise<{}>

  ngOnInit() {
  }

  chatwithbot(){
    this.stoplisten();
    this.conversation.push({"msg":this.input,"sentBy": "U"});
    this.loadingchat=true;
    this.service.getresponse(this.input).subscribe((res)=>{
     if(res){    
      this.loadingchat=false;
      if(this.checkbox1)
       this.speak.speak(res.answer,(err)=>{
         
        this.startlisen();
       })
    this.conversation.push({"msg":res.answer,"sentBy": "CB"});
    this.input="";
     }
     });
     
  }
  mikeonoff(){
    if(this.checkbox1){
    if(this.mikevalue)
    this.startlisen();
    else
    this.stoplisten();
    }else{
      alert("please switch on the toggle to continue voice")
    }
  }

  startlisen(){
    this.mikevalue=false;
    this.speak.record().subscribe((res)=>{
      this.input=res;
      this.chatwithbot();
      this.stoplisten();
    },(err)=>{
      this.stoplisten();
      this.startlisen();
    })
  }
  stoplisten(){
    this.mikevalue=true;
    this.speak.DestroySpeechObject();
  }

}
